<template>
  <div class="container">
    <div class="float_left">
      <div>
        <div class="float_right btn_container btn_check_in">
          <router-link :to="{name: 'reserved'}">
            <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/home/btn_check_in.png') + ')' }"></img-button>
          </router-link>
        </div>
      </div>
      <div style="clear:both;">
        <div class="float_right img_container2">
          <img :src="img_hotel_pic02">
        </div>
        <div class="float_right btn_container btn_extension">
          <router-link :to="{name: 'extension'}">
            <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/home/btn_extension.png') + ')' }"></img-button>
          </router-link>
        </div>
      </div>
    </div>
    <div class="float_left">
      <div class="img_container1">
        <img :src="img_hotel_pic01">
      </div>
      <div class="btn_container btn_check_out">
        <router-link :to="{name: 'extension'}">
          <img-button btn-type="primary" class="btn_huge" :style="{ backgroundImage: 'url(' + require('@/assets/home/btn_check_out.png') + ')' }"></img-button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
import img_hotel_pic01 from '@/assets/home/home_hotel_pic01.png'
import img_hotel_pic02 from '@/assets/home/home_hotel_pic02.png'

export default {
  data() {
    return {
      img_hotel_pic01,
      img_hotel_pic02
    };
  }
}
</script>

<style scoped>
.container {
  margin: 55px;
}

.float_left {
  float: left;
}

.float_right {
  float: right;
}

.el-row {
  margin-top: 0px;
}

.btn_container {
  padding: 16px;
}

.btn_huge {
  width: 400px;
  height: 230px;
  font-size: 32px;
}

.btn_check_in {
  background-color: #541F29;
}

.btn_check_out {
  background-color: #552068;
}

.btn_extension {
  vertical-align: top;
  background-color: #386046;
}

.img_container1 {
  width: 290px;
  background-color: #203065;
}

.img_container2 {
  background-color: #386046;
}
</style>



